  methods:{

    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
